<template>
  <div @click="goToDetail">
    <el-card class="card">
      <div class="image">
        <img :src='imageUrl' />
      </div>
      <div class="title">{{goods.title}}</div>
      <div class="stock">{{goods.stock}}</div>
      <div class="price">{{goods.price}}</div>
    </el-card>
  </div>
</template>

<script>
export default {
  props: ['goods'],
  computed: {
    imageUrl () {
      return `http://localhost:3000/uploads/${this.goods.filename}`
    }
  },
  methods: {
    goToDetail () {
      console.log('click')
      this.$router.push({path: `/detail/${this.goods._id}`})
    }
  }
}
</script>

<style>
.card {
  width: 100%;
  margin-top: 0.2rem;
}
.image {
  width: 100%;
}
.image > img {
  width: 100%;
}
</style>



